import Api from '@/lib/api'
const app = {
    namespaced: true,
    state: {
        fileDetail: {},
        taskLogs: [],
        noDatas: false,
        myTask: {
            myUpcomingCount: '',
            myApplyCount: '',
            myBookMr: ''
        },
        personnelInfo: {},
        userList: [],
        checkedUser: {},
        meetingInfo: {},
        smartSupervisionDetail: {startTime: '', attachList: []},
        smartSupervisionLogs: []
    },
    mutations: {
        setFileDetail (state, obj) {
            state.fileDetail = obj
        },
        setPersonnelInfo (state, obj) {
            state.personnelInfo = obj
        },
        setNoDatas (state, isNo) {
            state.noDatas = isNo
        },
        setTaskLogs (state, arr) {
            state.taskLogs = arr
        },
        setSmartSupervisionDetail (state, obj) {
            state.smartSupervisionDetail = obj
        },
        setSmartSupervisionLogs (state, arr) {
            state.smartSupervisionLogs = arr
        },
        setUserList (state, arr) {
            state.userList = arr
        },
        setCheckedUser (state, obj) {
            state.checkedUser = obj
        },
        setMyTask (state, obj) {
            state.myTask = obj
        },
        setMeetingInfo (state, obj) {
            state.meetingInfo = obj
        }
    },
    actions: {
        async getCommonFileDetail ({commit}, id) {
            const data = await Api.request('get', `/act/filetransfer/info/${id}`)
            sessionStorage.taskId = data.fileTransfer.taskId
            data.fileTransfer.content = data.fileTransfer.content.replace(/\n|\r\n/g, '<br/>')
            // data.fileTransfer.content = '上岛咖啡还是代理商理发师的粉红色的路过看到过发的快感地方看过地方看过对方考虑过代理费控股地方了挂掉了咖啡馆大富豪老公代理费控股寰谛凤翎可观的法律规划地方两个的卢浮宫到了咖啡馆到了咖啡馆得分感动死了发过火打了个地方两个大富豪老公的双方各对方考虑过到了咖啡馆代理费控股代理费控股的卢浮宫对方过后的考虑公司还单撸疯狂过后东方红路看过地方两个大发开了个会到了咖啡馆和到了咖啡馆和帝里风光好地方了开个会的看法两个的山沟里的恢复开工地方两个客户端是否老顾客帝里风光好到了咖啡馆和代理商疯狂过后鲁大师妇姑荷箪食卢浮宫货收到了疯狂过后代理费控股和独守空房工号的斯洛伐克国会大厦廊坊控股寰谛凤翎红红的咖啡馆代课老师搞活动覆盖客户第六感和第三方来开个会地方了规划的路口红绿灯老地方看后感的来访客户的胜利疯狂过后楼的看法和堕落腐化劳动法考核浪蝶狂蜂还高浪蝶狂蜂还高李达康李达康很反感辽阔的复合弓蓝卡队工号看到两个回来的快感离开的法国零售地方看过的看法和个老地方看后感浪蝶狂蜂工号浪蝶狂蜂工号打了卡复活娄底市分行离开对方广东佛山肯定发了获得舒服良好的法律货到付款和的来访客户劳动法考核老地方环境劳动法考核'
            commit('setFileDetail', data.fileTransfer)
            commit('setTaskLogs', data.taskLogs)
        },
        async getSmartSupervisionDetail ({commit}, id) {
            const data = await Api.request('get', `/act/task/info/${id}`)
            const { task, taskLogs, logList } = data
            sessionStorage.taskId = task.taskId
            sessionStorage.superviseId = task.id
            task.taskNote = task.taskNote.replace(/\n|\r\n/g, '<br/>')
            commit('setSmartSupervisionDetail', task)
            commit('setTaskLogs', taskLogs)
            commit('setSmartSupervisionLogs', logList)
        },
        async getCommonFileDetail4Any ({commit}, id) {
            const data = await Api.request('get', `/act/filetransfer/detail`, {id})
            data.fileTransfer.content = data.fileTransfer.content.replace(/\n|\r\n/g, '<br/>')
            commit('setFileDetail', data.fileTransfer)
            commit('setTaskLogs', data.taskLogs)
        },
        async getPersonnelInfo ({commit}, id) {
            const data = await Api.request('get', `/sys/contacts/detail/${id}`)
            const user = data.user
            user.homeplace = user.homeplace ? `我来自美丽的${user.homeplace}` : ''
            user.firstDatas = {
                userName: user.userName,
                gender: user.gender === '1' ? '男' : '女',
                jobNum: user.jobNum,
                departList: user.departList,
                position: user.position
            }
            user.secondDatas = {
                entryDay: user.entryDay,
                school: user.school,
                degree: user.degree,
                major: user.major
            }
            commit('setPersonnelInfo', user)
        },
        async getMyTask ({commit}) {
            const data = await Api.request('get', `/app/home/actCount`, {from: 'qywx'})
            commit('setMyTask', data)
        },
        async getMeetingInfo ({commit}, {id}) {
            const data = await Api.request('get', `/meeting/detail/${id}`)
            commit('setMeetingInfo', data.data)
        },
        async getUserList ({commit}, params) {
            const data = await Api.request('get', `/sys/contacts/list`, params)
            commit('setUserList', data.page.list)
            return data.page.list
        }
    }
}
export default app



