import Vue from 'vue'
import Router from 'vue-router'
import routers from './router'
import Util from '@/lib/util'

Vue.use(Router)

// 路由配置
const RouterConfig = {
    // mode: 'history',
    routes: routers
}

const router = new Router(RouterConfig)
router.beforeEach((to, from, next) => {
    Util.setTitle(to.meta.title)
    next()
})
router.afterEach(() => {
    window.scrollTo(0, 0)
})
export default router



