import Util from './util.js'
const api = {}
api.request = (type, url, params) => {
    return new Promise(async (resolve, reject) => {
        try {
            switch (type) {
            case 'get':
                params = {params}
                break
            default:
                break
            }
            const res = await Util.ajax[type](url, params)
            if (res.data) {
                resolve(res.data)
            } else {
                resolve(res)
            }
        } catch (error) {
            reject(error)
        }
    })
}
export default api



