<template>
    <button :class="'btn-'+(className)" @click="$emit('click')">{{text}}</button>
</template>

<script>
export default {
    name: 'Button',
    props: {
        className: {
            type: String,
            default: ''
        },
        text: {
            type: String,
            default: '确定'
        }
    },
    methods: {
    }
}
</script>

<style lang="stylus" scoped>
button
    width 690px
    border-radius 8px
    font-size 36px
    height 80px
    line-height 80px
    margin 0 30px
    &.btn-default
        border 1.5px grey solid
        background white
        color a-grey
    &.btn-primary
        border none
        background blue
        color white
    &.btn-primary-outline
        border 1.5px blue solid
        color blue
        background white
    &.btn-red-outline
        border 1.5px b-red solid
        color b-red
        background c-red
    &.btn-green-outline
        border 1.5px green solid
        color green
        background c-white
</style>



