<template>
    <div v-transfer-dom :data-transfer="transfer">
        <transition name="fade">
            <div class="mask" :class="mode+'-mode'" v-if="show" @click="$emit('on-hide')">
                <div class="wrap">
                    <div class="body"><slot name="body">{{content}}</slot></div>
                    <slot name="footer" v-if="showFooter" class="footer">
                        <Button text='取消' className="primary-outline" @click="onCancel"></Button>
                        <Button text='确定' className="primary" @click="onOk"></Button>
                    </slot>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
import TransferDom from '@/directives/transfer-dom'
import Button from '@/components/Button'
export default {
    name: 'Search',
    components: {
        Button
    },
    directives: {
        TransferDom
    },
    props: {
        transfer: {
            type: Boolean,
            default: false
        },
        show: {
            type: Boolean,
            default: false
        },
        showFooter: {
            type: Boolean,
            default: true
        },
        mode: {
            type: String,
            default: 'default'
        },
        content: {
            type: String,
            default: ''
        },
        onOk: {
            type: Function,
            default: () => {}
        },
        onCancel: {
            type: Function,
            default: () => {}
        }
    },
    methods: {
    }
}
</script>
<style lang="stylus" scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.mask
    position fixed
    top 0
    bottom 0
    left 0
    right 0
    &.default-mode
        background-color rgba(55,55,55,.6)
    &.img-mode
        background r-black
    width 100%
    height 100%
    z-index 1000
    .wrap
        position relative
        width 100%
        height 100%
        // width 500px
        // height 300px
        // background white
        // border-radius 8px
        .body
            position absolute
            top 50%
            left 50%
            transform translate(-50%, -50%)
            text-align center
            font-size 36px
            color black
            // line-height 200px
            // border-bottom 1.5px grey solid
        .footer
            display flex
            justify-content space-around
            padding 20px 0
            button
                height 60px
                line-height 60px
</style>



