import Home from '@/view/home'
// import SingleUpload from '@/view/singleUpload'

const routers = [
    {
        path: '/home',
        name: 'home',
        component: Home
    },
    // {
    //     path: '/singleUpload',
    //     name: 'singleUpload',
    //     component: SingleUpload
    // },
    {
        path: '**',
        redirect: '/'
    }
]

export default routers



