<template>
  <div>
    <transition :name="mergedOption.transition">
      <div
      :id="mergedOption.id"
      class="et-wrapper"
      :class="clazz"
      :transition="mergedOption.transition"
      v-show="showing"
      >
        <div class="et-content">
            <div class="i-wrap" v-if="mergedOption.iconName">
                <!-- <i :class="'icon-'+mergedOption.iconName"></i> -->
                <svg class="icon" aria-hidden="true">
                    <use :xlink:href="'#icon-'+mergedOption.iconName"></use>
                </svg>
            </div>
            <p>{{mergedOption.message}}</p>
        </div>
        <a class="et-close" v-if="mergedOption.closeable" v-on:click="close()">&times;</a>
      </div>
    </transition>
  </div>
</template>

<script>
const DEFAULT_OPT = {
    id: 'easy-toast-default',
    className: '',
    horizontalPosition: 'center',
    verticalPosition: 'top',
    parent: 'body',
    iconName: '',
    transition: 'fade',
    duration: 2500,
    message: '',
    closeable: false
}
export default {
    DEFAULT_OPT: DEFAULT_OPT,
    data () {
        return {
            queue: [],
            option: {},
            showing: false
        }
    },
    computed: {
        mergedOption: function () {
            return Object.assign({}, DEFAULT_OPT, this.option)
        },
        clazz: function () {
            let clazz = []
            let className = this.option.className
            let horizontalPosition = this.mergedOption.horizontalPosition
            let verticalPosition = this.mergedOption.verticalPosition
            let closeable = this.mergedOption.closeable
            if (className) {
                if (typeof className === 'string') {
                    clazz.push(className)
                }
                if (Array.isArray(className)) {
                    clazz = clazz.concat(className)
                }
            }
            if (horizontalPosition) {
                clazz.push(`et-${horizontalPosition}`)
            }
            if (closeable) {
                clazz.push('et-closeable')
            }
            if (verticalPosition) {
                clazz.push(`et-${verticalPosition}`)
            }
            return clazz.join(' ')
        }
    },
    methods: {
        close: function () {
            this.showing = false
            this.queue.shift()
        }
    },
    watch: {
        queue: function () {
            let pending = this.queue.length
            if (pending === 0) {
                return
            }
            this.showing = true
            this.option = this.queue[0]
            if (
                (!this.option.mode || this.option.mode === 'override') &&
                pending > 1
            ) {
                clearTimeout(this.timeoutId)
                this.showing = false
                this.timeoutId = null
                this.timeoutId = setTimeout(() => this.queue.shift())
            } else {
                this.timeoutId = setTimeout(() => {
                    this.showing = false
                    this.timeoutId = null
                    setTimeout(() => this.queue.shift())
                }, this.mergedOption.duration)
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
    .et-wrapper
        position fixed
        top 80%
        background-color rgba(48, 45, 55, 0.7)
        border-radius 10px
        padding 24px 20px
        display flex
        max-width 500px
        min-width: 144px
        color white
        z-index 9999999
        margin auto
        text-align center
        .et-content
            font-size 30px
            line-height 30px
            text-align center
            width 100%
            .i-wrap
                font-size 60px
                width 100%
                height 60px
                line-height 60px
                margin-bottom 6px
        .et-close
            position absolute
            top 0
            right 5px
            color white
        &.et-left
            right auto
            left 0
        &.et-right
            right 0
            right auto
        &.et-center
            left 50%
            transform translateX(-50%)
        &.et-top
            top 200px
            buttom auto
        &.et-bottom
            buttom 0
            top auto 
        &.et-alert
            background-color rgba(189, 19, 19, 0.7)
        &.et-warn
            background-color rgba(250, 180 ,90 ,0.9)
        &.et-info
            background-color rgba(32, 83, 201, 0.7)
    .fade-enter-active,
    .fade-leave-active,
    .fade-transition {
      -webkit-transition: opacity .3s ease;
      transition: opacity .3s ease;
    }
    .fade-enter,
    .fade-leave,
    .fade-leave-active {
      opacity: 0;
    }
    .slide-down-enter-active,
    .slide-down-leave-active,
    .slide-down-transition {
      -webkit-transition: opacity .3s ease, top .3s ease-in;
      transition: opacity .3s ease, top .3s ease-in;
    }
    .slide-down-leave-active,
    .slide-down-enter,
    .slide-down-leave {
      opacity: 0;
      top: -10% !important;
    }
    .slide-up-enter-active,
    .slide-up-leave-active,
    .slide-up-transition {
      -webkit-transition: opacity .3s ease, top .3s ease-in;
      transition: opacity .3s ease, top .3s ease-in;
    }
    .slide-up-leave-active,
    .slide-up-enter,
    .slide-up-leave {
      opacity: 0;
      top: 110% !important;
    }
    .slide-left-enter-active,
    .slide-left-leave-active,
    .slide-left-transition {
      -webkit-transition: opacity .3s ease, left .3s ease-in;
      transition: opacity .3s ease, left .3s ease-in;
    }
    .slide-left-leave-active,
    .slide-left-enter,
    .slide-left-leave {
      opacity: 0;
      left: 110% !important;
    }
    .slide-right-enter-active,
    .slide-right-leave-active,
    .slide-right-transition {
      -webkit-transition: opacity .3s ease, left .3s ease;
      transition: opacity .3s ease, left .3s ease;
    }
    .slide-right-leave-active,
    .slide-right-enter,
    .slide-right-leave {
      opacity: 0;
      left: -1000px !important;
    }        

</style>



