<template>
    <div class="home">
        <div v-for="(item, index) in users" :key="item._id">
            <p>{{item.name}}</p>
            <img alt="暂无" :src="item.imgUrl">
            <div class="mask">
                <svg class="icon chakan" aria-hidden="true" @click="handleClick('1', item, index)">
                    <use xlink:href="#icon-chakan"></use>
                </svg>
                <svg class="icon icon-paizhao" aria-hidden="true" @click="handleClick('2', item, index)">
                    <use xlink:href="#icon-paizhao"></use>
                </svg>
            </div>
            <input ref="file" type="file" @change="(e) => uploadImg(e, item)">
        </div>
        <Modal @on-hide="isOpenImg = false" :transfer="true" :show-footer="false" :show="isOpenImg" mode="img">
            <img class="" slot="body" width="375" height="500" :src="chakanImg">
        </Modal>
    </div>
</template>
<script>
import Api from '@/lib/api'
import Modal from '@/components/Modal'
export default {
    name: 'home',
    components: {
        Modal
    },
    data () {
        return {
            isOpenImg: false,
            chakanImg: '',
            users: [],
            uploads: []    
        }
    },
    async mounted () {
        const users = await Api.request('get', 'get/users')
        const uploads = await Api.request('get', 'get/dailyupload')
        uploads.map((item) => {
            users.map((item1) => {
                if (item1.name === item.name) {
                    item1.imgUrl = item.imgUrl
                }
            })
        })
        this.users = users
    },
    methods: {
        handleClick (type, item, index) {
            switch (type) {
            case '1':
                console.log('查看')
                if (item.imgUrl) {
                    this.isOpenImg = true
                    this.chakanImg = item.imgUrl
                }
                break
            default:
                console.log('拍照')
                this.$refs.file[index].click()
                break
            }
        },
        uploadImg (e, item) {
            const acceptTypes = ['image/jpg', 'image/jpeg', 'image/png']
            const file = e.target.files[0]
            if (file) {
                const fileType = file.type
                if (acceptTypes.indexOf(fileType) === -1) {
                    this.$toast('上传文件格式错误~')
                    return false
                }
            } else {
                return false
            }
            
            const formData = new FormData()
            formData.append('name', item.name)
            formData.append('file', file)
            fetch('http://248064j1y0.wicp.vip/upload', {
                method: 'POST', // or 'PUT'
                body: formData
            }).then(response => response.json())
                .then(response => {
                    item.imgUrl = response.data.fileUrl
                    this.$forceUpdate()
                })
                .catch(error => console.error('Error:', error))
        }
    }
}
</script>
<style lang="stylus">
.home
    font-size 28px
    display flex
    justify-content space-around
    flex-wrap wrap
    >div
        position relative
        margin-top 24px
        >p
            padding 10px
        >img
            display block
            width 200px
            height 300px   
        .mask
            position absolute
            left 0
            bottom 0
            background rgba(0, 0, 0, .1)
            text-align center
            font-size 48px
            width 200px
            height 300px
            line-height 300px
            svg
                color black
                opacity .5
                &.chakan
                    margin-right 10px
        >input
            position absolute
            left -9999px        
</style>



