import axios from 'axios'
const util = {}
const env = process.env.NODE_ENV

util.getBackDomain = function () {
    switch (env) {
    case 'production':
    default:
        return `//248064j1y0.wicp.vip`
        // return `//10.70.128.51:8088`
    }
}
util.getBackUrl = function () {
    return `${this.getBackDomain()}/`
}

const ajaxUrl = util.getBackUrl()

util.ajax = axios.create({
    baseURL: ajaxUrl,
    timeout: 30000
})

let ajaxCount = 0
util.ajax.interceptors.request.use(function (config) {
    // 在发送请求之前做些什么
    if (!config.params || !config.params.notShowLoading) {
        ajaxCount === 0 && (util.loader = util.vm.$loading.show())
        ajaxCount++
    }
    if (config.params && config.params.hasOwnProperty('notShowLoading')) {
        delete config.params.notShowLoading
    }
    const userInfo = sessionStorage.userInfo && JSON.parse(sessionStorage.userInfo)
    config.headers.token = '' || (userInfo && userInfo.token)
    config.headers.act_token = '' || (userInfo && userInfo.actToken)
    return config
}, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error)
})

util.ajax.interceptors.response.use((config) => {
    if (--ajaxCount === 0) {
        util.loader && util.loader.hide()
    }
    // debugger
    const { vm } = util
    if (config.data.code !== '1000') {
        vm.$toast(config.data.msg)
        return false
    } else {
        return config.data
    }
}, (error) => {
    return Promise.reject(error)
})
util.setTitle = function (title) {
    title = title || '智慧充电'
    window.document.title = title
    var i = document.createElement('iframe')
    // i.src = '//m.baidu.com/favicon.ico'
    i.style.display = 'none'
    i.onload = () => {
        setTimeout(() => {
            i.remove()
        }, 9)
    }
    document.body.appendChild(i)
}
util.getReqParam = function (name) {
    var url = location.search
    if (url.indexOf('?') !== -1) {
        var str = url.split('?')
        var strs = str[1].split('&')
        for (var i = 0; i < strs.length; i++) {
            if (name === strs[i].split('=')[0]) return strs[i].split('=')[1]
        }
    }
    return null
}
util.formatDate = function (date = new Date(), fmt = 'yyyy-MM-DD hh:mm:ss') {
    const o = {
        'M+': date.getMonth() + 1, // 月份
        'D+': date.getDay(), // 日
        'h+': date.getHours(), // hours
        'm+': date.getMinutes(), // 分钟
        's+': date.getSeconds() // 秒,
    }

    if (/(y+)/.test(fmt)) {
        // RegExp.$1 是RegExp的一个属性,指的是与正则表达式匹配的第一个 子匹配(以括号为标志)字符串，以此类推，RegExp.$2，RegExp.$3，..RegExp.$99总共可以有99个匹配
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    for (let k in o) {
        if (new RegExp(`(${k})`).test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
        }
    }
    return fmt
}
export default util



